<template>
  <div class="user-profile">
    <h2>ÊúÉÂì°Ë≥áÊñô</h2>
    <div v-if="user">
      <p><strong>ÈõªÂ≠êÈÉµ‰ª∂Ôºö</strong> {{ user.email }}</p>
      <h3>ÊàëÁöÑÂÖâÊòéÁáà</h3>
      <div v-if="userLanterns.length" class="user-lanterns">
        <div v-for="lantern in userLanterns" :key="lantern.id" class="lantern-item">
          <h4>{{ lantern.name }}</h4>
          <p>ÈªûÁáàÊó•ÊúüÔºö{{ lantern.date }}</p>
          <p>Á•àÁ¶èÁïôË®ÄÔºö{{ lantern.message }}</p>
        </div>
      </div>
      <p v-else>ÊÇ®ÈÇÑÊ≤íÊúâÈªû‰∫Æ‰ªª‰ΩïÂÖâÊòéÁáà„ÄÇ</p>
    </div>
    <p v-else>ËºâÂÖ•‰∏≠...</p>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue';
import { useStore } from 'vuex';
import { userApi } from '@/services/api';

export default {
  name: 'UserProfile',
  setup() {
    const store = useStore();
    const user = ref(null);
    const userLanterns = ref([]);

    onMounted(async () => {
      user.value = store.state.user;
      if (user.value) {
        userLanterns.value = await userApi.getUserLanterns(user.value.id);
      }
    });

    return { user, userLanterns };
  }
}
</script>

