<template>
  <div class="lantern-management">
    <h3>ÂÖâÊòéÁáàÁÆ°ÁêÜ</h3>
    <button @click="showAddForm = true">Êñ∞Â¢ûÂÖâÊòéÁáà</button>
    <div v-if="showAddForm">
      <h4>Êñ∞Â¢ûÂÖâÊòéÁáà</h4>
      <form @submit.prevent="addLantern">
        <input v-model="newLantern.name" placeholder="ÂêçÁ®±" required>
        <input v-model="newLantern.description" placeholder="ÊèèËø∞" required>
        <input v-model.number="newLantern.price" type="number" placeholder="ÂÉπÊ†º" required>
        <button type="submit">Êñ∞Â¢û</button>
      </form>
    </div>
    <table>
      <thead>
        <tr>
          <th>ID</th>
          <th>ÂêçÁ®±</th>
          <th>ÊèèËø∞</th>
          <th>ÂÉπÊ†º</th>
          <th>Êìç‰Ωú</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="lantern in lanterns" :key="lantern.id">
          <td>{{ lantern.id }}</td>
          <td>{{ lantern.name }}</td>
          <td>{{ lantern.description }}</td>
          <td>${{ lantern.price }}</td>
          <td>
            <button @click="editLantern(lantern)">Á∑®ËºØ</button>
            <button @click="deleteLantern(lantern.id)">Âà™Èô§</button>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue';
import { lanternApi } from '@/services/api';

export default {
  name: 'LanternManagement',
  setup() {
    const lanterns = ref([]);
    const showAddForm = ref(false);
    const newLantern = ref({ name: '', description: '', price: 0 });

    onMounted(async () => {
      lanterns.value = await lanternApi.getLanterns();
    });

    const addLantern = async () => {
      const addedLantern = await lanternApi.createLantern(newLantern.value);
      lanterns.value.push(addedLantern);
      newLantern.value = { name: '', description: '', price: 0 };
      showAddForm.value = false;
    };

    const editLantern = (lantern) => {
      // ÂØ¶ÁèæÁ∑®ËºØÂÖâÊòéÁáàÁöÑÈÇèËºØ
      console.log('Á∑®ËºØÂÖâÊòéÁáà', lantern);
    };

    const deleteLantern = async (lanternId) => {
      if (confirm('Á¢∫ÂÆöË¶ÅÂà™Èô§Ê≠§ÂÖâÊòéÁáàÂóéÔºü')) {
        await lanternApi.deleteLantern(lanternId);
        lanterns.value = lanterns.value.filter(lantern => lantern.id !== lanternId);
      }
    };

    return { lanterns, showAddForm, newLantern, addLantern, editLantern, deleteLantern };
  }
}
</script>