  <div class="register">
    <h2>Ë®ªÂÜäÊñ∞Â∏≥Ëôü</h2>
    <form @submit.prevent="register">
      <input v-model="email" type="email" placeholder="ÈõªÂ≠êÈÉµ‰ª∂" required>
      <input v-model="password" type="password" placeholder="ÂØÜÁ¢º" required>
      <input v-model="confirmPassword" type="password" placeholder="Á¢∫Ë™çÂØÜÁ¢º" required>
      <button type="submit">Ë®ªÂÜä</button>
    </form>
    <router-link to="/login">Â∑≤ÊúâÂ∏≥ËôüÔºüÁôªÂÖ•</router-link>
